const daiyousei = {
  name: "大妖精",
  intro: "路过的较强妖精",
  detail: "回合初获得[自然加护]。",
  type: "elite",
  scene: ["shrine", "town"],
  growth: {
    health: 1.5,
    attack: 2,
    speed: 1.3
  },
  onload: (Admin, index) => {
    Admin.enermy.buff.of(index).set("natral_protect", 4);
  }, 
  action: (Admin, index) => {
    Admin.enermy.buff.of(index).set("natral_protect", 4);
    Admin.role.damage.target(0).from(index).by({
      type: "scale",
      value: 80,
    });
  }
}

export default daiyousei;