<script>
  import { Admin, Explain } from "../../store";
  import { onMount, beforeUpdate } from "svelte";
  import Collection from "../../data/collection.json";
  import Equipment from "../../data/equipment.js";
  import Souvenir from "../../data/souvenir.json";
  import Spellcard from "../../data/spellcard.js";
  import Consumable from "../../data/consumable.json";
  import Basecard from "../../data/basecard.js";
  import Card from "../card.svelte";

  export let info;

  let product;
  let count = 2;
  const element = [];

  if (info.cache.product) product = info.cache.product;
  else info.cache.product = summonProduct();
  if (typeof info.cache.count == "number") count = info.cache.count;

  onMount(function () {
    element.forEach((e, i) => {
      e.onclick = (_) => purchase(product[i]);
    });
  });

  function summonProduct() {
    let source = Object.keys(Spellcard)
      .filter((s) => Spellcard[s].role == $Admin.data.role)
      .rd()
      .splice(0, $Admin.data.sugar + 3);
    let res = source.map((c) => {
      return {
        key: c,
        sold: false,
      };
    });
    product = $Admin.discount(res);
    return res;
  }
  function purchase(p) {
    if (!p.sold) {
      if (count != 0) {
        product = product;
        p.key.increaseOf($Admin.data.card);
        msg({ content: `已学习[${Spellcard[p.key].name}]` });
        p.sold = true;
        count--;
        info.cache.count = count;
      } else msg({ content: "时间精力不足！" });
    }
  }
</script>

<div class="content">
  <txt>{info.detail}</txt>
  <txt>大概还能学习&ensp;{count}&ensp;种符卡。</txt>
  <div class="products">
    {#each product as p, i (i)}
      <div bind:this={element[i]} class="product">
        <Card
          key={p.key}
          cover={p.sold && {
            name: "空",
            detail: "已学习",
          }}
        />
      </div>
    {/each}
  </div>
</div>

<style>
  .content {
    z-index: 1;
    width: 600px;
    height: 500px;
    margin: auto;
  }
  txt {
    font-size: 20px;
    line-height: 30px;
    position: relative;
    top: 2px;
    margin-left: 5px;
  }
  .products {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 600px;
    margin-top: 10px;
  }
  .product {
    cursor: pointer;
    margin: 0px 10px;
  }
</style>
