<script>
  import { onMount, beforeUpdate } from "svelte";
  import { Admin, Explain } from "../store";
  import spellcard from "../data/spellcard.js";
  import basecard from "../data/basecard.js";
  import { fade, scale } from "svelte/transition";
  import sector from "../data/sector";

  let card = Object.assign(deepCopy(spellcard), deepCopy(basecard));

  export let key;
  export let action = (_) => null;
  export let select = (_) => null;
  export let cost = false;
  export let transform = {};
  export let animate = true;
  export let interim = card[key].interim;
  export let abandoned = false;
  export let cover = false;
  let used = false;
  let _style;
  let element;
  let transition = "0.3s";
  let ready = false;
  let drag = false;
  let element_cost;

  $: role = Object.keys(spellcard).includes(key) ? spellcard[key].role : "base";
  $: costFix = typeof cost == "number" ? cost : card[key].cost;

  onMount((_) => {
    let enterEvent = (_) => {
      transform.scale = 1.1;
      let recover = (_) => {
        transform.scale = 1;
      };
      element.onmouseleave = recover;
      element.ontouchend = recover;
    };
    element.onmouseenter = enterEvent;
    let dragEvent = (event) => {
      drag = true;
      select(true);
      const t = deepCopy(transform);
      if (!event.x) event.x = event.touches[0].pageX;
      if (!event.y) event.y = event.touches[0].pageY;
      const x = event.x;
      const y = event.y;
      transform.rotate = 0;
      transform.y += Math.sin((t.rotate / 180) * Math.PI) * t.x;
      transform.x -= Math.tan((t.rotate / 180) * Math.PI) * t.y;
      transform.scale = 1.1;
      _style = `z-index:100;`;
      let moveEvent = (e) => {
        if (!e.x) e.x = e.touches[0].pageX;
        if (!e.y) e.y = e.touches[0].pageY;
        ready = typeof $Admin.enermy.checkForPosition(e) == "number";
        let offsetX = e.x - x;
        let offsetY = e.y - y;
        transition = "transform 0.3s";
        if ($Admin.battle.round.get()) {
          if ($Admin.mobile) {
            _style = `left:${235 + offsetY / $Admin.mobile}px;top:${-offsetX / $Admin.mobile}px;z-index:100;`;
          } else
            _style = `left:${235 + offsetX}px;top:${offsetY}px;z-index:100;`;
        }
      };
      element.onmousemove = moveEvent;
      element.ontouchmove = moveEvent;
      const cancel = (_) => {
        ready = false;
        drag = false;
        if (element) {
          element.onmousedown = dragEvent;
          element.onmousemove = null;
          element.onmouseup = null;
          element.onmouseleave = null;
          element.ontouchstart = dragEvent;
          element.ontouchmove = null;
          element.ontouchend = null;
        }
        select(false);
        transform = t;
        transform.scale = 1;
        transition = "0.3s";
        _style = `left:235px;top:0px;`;
      };
      let leaveEvent = (e) => {
        if (!e.x) e.x = e.changedTouches[0].pageX;
        if (!e.y) e.y = e.changedTouches[0].pageY;
        let _cost = $Admin.handcard.Cost();
        if (_cost === false) {
          cancel();
          msg({ content: "条件不足" });
        } else if ($Admin.role.power.of(0).get() >= _cost) {
          let res = $Admin.enermy.checkForPosition(e);
          if (typeof res == "number") {
            element.onmousedown = null;
            element.onmousemove = null;
            element.onmouseleave = null;
            used = true;
            action(res);
            setTimeout((_) => {
              used = false;
              cancel();
            }, 500);
          } else cancel();
        } else {
          cancel();
          msg({ content: "灵力不足" });
        }
      };
      element.onmouseup = leaveEvent;
      element.ontouchend = leaveEvent;
      element.onmouseleave = cancel;
    };
    if (Object.keys(transform).length == 0) dragEvent = (_) => null;
    element.onmousedown = dragEvent;
    element.ontouchstart = (e) => {
      transform.scale = 1.1;
      dragEvent(e);
    };
  });
  beforeUpdate((_) => {
    if (used) {
      transform.scale = 1.1;
      ready = true;
    }
    if (interim && element_cost) {
      $Explain(element_cost).with({
        name: "一次性",
        detail: "这张牌是消耗品，打出后不会再抽到。",
      });
    }
  });
</script>

<div
  class={`card ${cover ? "cover" : role} ${used && "used"} ${ready && "ready"} ${
    !animate && "animateOff"
  } ${drag && "drag"} ${abandoned && "abandoned"}`}
  style="transform:{'rotate' in transform
    ? `rotate(${transform.rotate}deg)`
    : ``}translate({transform.x}px,
  {transform.y}px){'scale' in transform
    ? `scale(${transform.scale})`
    : ``};transition:{animate ? transition : 'none'};{_style};"
  bind:this={element}
  in:scale={{ duration: 250 }}
>
  <img src="/img/lotr_hobbit_stamp.svg" />
  <div class="content">
    <icon class="icon-{cover ? 'logo' : role == 'base' ? 'base' : key}"></icon>
    <txt class="name">{cover ? cover.name : card[key].name}</txt>
    <div class="cost {!cover && interim && 'interim'}" bind:this={element_cost}>
      <txt>{cover ? 0 : costFix}</txt>
    </div>
  </div>
  <div class="detail">
    <txt>{cover ? cover.detail : card[key].detail}</txt>
  </div>
</div>

<style>
  .card {
    align-items: center;
    filter: drop-shadow(0 0 6px #555);
    transition: 0.3s;
  }
  .drag,
  .drag:hover {
    cursor: none;
  }
  .card::before {
    content: "";
    -webkit-mask: url("/img/urban-sprite.webp");
    mask: url("/img/urban-sprite.webp");
    -webkit-mask-size: 3000% 100%;
    mask-size: 3000% 100%;
    animation: used 0.5s steps(29) forwards;
  }
  .ready .name,
  .ready icon {
    text-shadow: none;
    filter: drop-shadow(0 0 2px #fff) drop-shadow(0 0 4px #fff)
      drop-shadow(0 0 8px #fff) drop-shadow(0 0 16px #fff);
  }
  .animateOff .name,
  .animateOff icon {
    transition: none;
  }
  img {
    width: 130px;
    height: 190px;
    filter: brightness(1.1);
  }
  .content {
    position: absolute;
    top: 20px;
    width: 105px;
    height: 95px;
    background-color: #777;
    align-items: center;
  }
  icon,
  .name {
    color: white;
    text-shadow:
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow);
    transition: 0.3s;
  }
  icon {
    font-size: 60px;
    height: 60px;
    margin-top: 5px;
  }
  .name {
    font-family: title;
    font-size: 16px;
    font-weight: 500;
    line-height: 28px;
  }
  .detail {
    position: absolute;
    bottom: 22px;
    align-items: center;
    justify-content: center;
    width: 111px;
    height: 48px;
    font-size: 12px;
    text-align: center;
    white-space: pre-wrap;
    word-break: break-all;
    font-family: remark;
    color: #555;
    z-index: 1;
  }
  .cost {
    position: absolute;
    left: 0;
    width: 35px;
    height: 35px;
    background-color: #faea93;
    z-index: 1;
    clip-path: polygon(0 0, 100% 0, 0 100%);
    color: #777;
  }
  .cost txt {
    width: 20px;
    font-size: 18px;
    line-height: 20px;
    text-align: center;
    font-family: title;
  }
  .interim {
    background-color: hsl(51, 12%, 90%);
    color: #666;
  }
  .used {
    -webkit-mask: url("/img/urban-sprite.webp");
    mask: url("/img/urban-sprite.webp");
    -webkit-mask-size: 3000% 100%;
    mask-size: 3000% 100%;
    animation: used 0.5s steps(29) forwards;
  }
  .abandoned {
    animation: used 0.4s steps(22) forwards;
    -webkit-mask: url("/img/nature-sprite.webp");
    mask: url("/img/nature-sprite.webp");
    -webkit-mask-size: 2300% 100%;
    mask-size: 2300% 100%;
  }

  @keyframes huerotate {
    0% {
      filter: hue-rotate(0deg);
    }

    100% {
      filter: hue-rotate(360deg);
    }
  }
  @keyframes used {
    0% {
      -webkit-mask-position: 0 0;
      mask-position: 0 0;
    }

    100% {
      -webkit-mask-position: 100% 0;
      mask-position: 100% 0;
    }
  }
  @keyframes animate_spellcard {
    0% {
      opacity: 0;
      top: 100px;
    }

    100% {
      opacity: 1;
      top: 0;
    }
  }
  @keyframes useSpellcard {
    0% {
      transform: translateY(-20px);
    }
    50% {
      transform: rotateY(90deg) translateY(-20px);
    }

    100% {
      transform: rotateY(90deg) translateY(-20px);
      width: 0;
      margin: 0;
      border: 0;
    }
  }

  .cover .content {
    background-image: radial-gradient(
      ellipse at bottom,
      #1b2735 0%,
      #090a0f 100%
    );
  }
  .base .content {
    --name: #333;
    --shadow: #555;
