  .alice .content {
    --name: #777;
    --color1: rgb(255 239 186);
    --color2: rgb(151 150 204);
    --color3: rgb(255, 211, 246);
    --color4: rgb(150, 184, 204);
    --shadow: #666;
    background-color: rgb(242 142 146);
    background-image: /* Pink lines */
      linear-gradient(
        -116deg,
        transparent 40%,
        var(--color3) 0,
        var(--color3) 42%,
        transparent 42%
      ),
      linear-gradient(
        116deg,
        transparent 41%,
        var(--color3) 0,
        var(--color3) 43%,
        transparent 43%
      ),
      /* Black lines */
        linear-gradient(
          -116deg,
          transparent 40%,
          var(--color4) 0,
          var(--color4) 42%,
          transparent 42%
        ),
      linear-gradient(
        116deg,
        transparent 41%,
        var(--color4) 41%,
        var(--color4) 43%,
        transparent 43%
      ),
      /* Black diamonds */
        linear-gradient(-115deg, var(--color2) 16.5%, transparent 0),
      linear-gradient(-65deg, var(--color2) 16.5%, transparent 0),
      linear-gradient(115deg, var(--color2) 16.5%, transparent 0),
      linear-gradient(65deg, var(--color2) 16.5%, transparent 0),
      /* Pink diamonds */
        linear-gradient(-115deg, var(--color1) 16.5%, transparent 0),
      linear-gradient(-65deg, var(--color1) 16.5%, transparent 0),
      linear-gradient(115deg, var(--color1) 16.5%, transparent 0),
      linear-gradient(65deg, var(--color1) 16.5%, transparent 0);
    background-size: /* Pink lines */
      8em 8em,
      8em 8em,
      /* Black Lines */ 8em 8em,
      8em 8em,
      /* Black diamonds */ 8em 8em,
      8em 8em,
      8em 8em,
      8em 8em,
      /* Pink diamonds */ 8em 8em,
      8em 8em,
      8em 8em,
      8em 8em;
    background-position: /* Pink lines */
      3em -8em,
      -3em -8em,
      /* Black Lines */ -9em 8em,
      9em 8em,
      /* Black diamonds */ 0,
      0,
      0,
      0,
      /* Pink diamonds */ 4em,
      4em,
      4em,
      4em;
    background-repeat: repeat !important;
  }
