<script>
  import { onMount, beforeUpdate, afterUpdate } from "svelte";
  import { fade, scale } from "svelte/transition";
  import { Explain, archive, Admin, setting, explore, page } from "../store";
  import Card from "../addon/card.svelte";
  import Data from "../data/data";

  const remark = {
    damage_total: "造成伤害总量",
    damage_frequency: "造成伤害总次数",
    max_damage: "造成的最高伤害",
    hurt_total: "受到伤害总量",
    hurt_frequency: "受到伤害总次数",
    max_hurt: "受到的最高伤害",
    cost_total: "消耗灵力总数",
    punch_count: "打出手牌总数",
    battle_count: "战斗总次数",
    round_total: "回合总数",
    defeat_total: "击败敌人总数",
    max_lv: "敌人最高等级",
    coin_reward_total: "获得硬币总数",
    collection_reward_total: "获得收藏品总数",
  };
  let element_paper;
  let selected_record;
  let record_index = "statistics";
  const element_item = [];
  const element_container = {};
  const item = {};
  Object.assign(item, Data.collection);
  Object.assign(item, Data.equipment);
  Object.assign(item, Data.souvenir);

  onMount(function () {
    element_paper.style.top = `${document.body.clientHeight}px`;
    element_paper.style.transition = "0.3s";
  });
  afterUpdate((_) => {
    for (let element in element_container) {
      if (element_container[element])
      element_container[element].ontouchstart = function (event) {
        let originX = event.touches[0].pageX;
        let scrollTop = element_container[element].scrollTop;
        element_container[element].ontouchmove = function (e) {
          let x = e.touches[0].pageX;
          element_container[element].scrollTop = scrollTop + x - originX;
        };
      };
    }
  });

  function foldPaper() {
    element_paper.style.top = `${document.body.clientHeight}px`;
    selected_record = null;
  }
  function seleteRecord(i) {
    element_paper.style.top = `${document.body.clientHeight - 640}px`;
    selected_record = i;
  }
</script>

<div
  class="body archive"
  in:fade={{ duration: 250 }}
  out:fade={{ duration: 250 }}
>
  <div
    class="paper_close"
    on:click={foldPaper}
    style="z-index:{typeof selected_record == 'number' ? 1 : -1}"
  ></div>
  <div class="paper" bind:this={element_paper}>
    <div class="event_fold" on:click={foldPaper}>×</div>
    {#if typeof selected_record == "number"}
      <div class="title">
        <txt>{$archive.record[selected_record].name}</txt>
        <span
          >&ensp;选项个数: {$archive.record[selected_record].player_data
            .sugar}</span
        >
      </div>
      <div class="record_body">
        <div class="record_index">
          <txt
            class={record_index == "statistics" && "selected"}
            on:click={(_) => (record_index = "statistics")}>统计</txt
          >
          <txt
            class={record_index == "card" && "selected"}
            on:click={(_) => (record_index = "card")}>牌库</txt
          >
          <txt
            class={record_index == "item" && "selected"}
            on:click={(_) => (record_index = "item")}>物品</txt
          >
        </div>
        {#if record_index == "statistics"}
          <div class="statistics">
            {#each Object.keys(remark) as s, i (i)}
              <div>
                <txt>{remark[s]}</txt>
                <txt
                  >{window.retain(
                    $archive.record[selected_record].battle_data[s],
                    0,
                  )}</txt
                >
              </div>
            {/each}
          </div>
        {/if}
        {#if record_index == "card"}
          <div class="card container" bind:this={element_container.card}>
            {#each Object.unCount($archive.record[selected_record].player_data.card) as c, i (i)}
              <Card key={c} />
            {/each}
          </div>
        {/if}
        {#if record_index == "item"}
          <div class="item container" bind:this={element_container.item}>
            {#each Object.keys($archive.record[selected_record].player_data.collection) as c, i (i)}
              <div
                bind:this={element_item[i]}
                style="background-color:var(--{item[c].type})"
              >
                <icon class="icon-{c}"></icon>
                {#if $archive.record[selected_record].player_data.collection[c] > 1}
                  <txt
                    >{$archive.record[selected_record].player_data.collection[
                      c
                    ]}</txt
                  >
                {/if}
              </div>
            {/each}
            {#if $archive.record[selected_record].player_data.equipment}
              <div
                bind:this={element_item[
                  Object.keys(
                    $archive.record[selected_record].player_data.collection,
                  ).length
                ]}
                style="background-color:var(--{item[
                  $archive.record[selected_record].player_data.equipment
                ].type})"
              >
                <icon
                  class="icon-{$archive.record[selected_record].player_data
                    .equipment}"
                ></icon>
              </div>
            {/if}
            {#if $archive.record[selected_record].player_data.souvenir}
              <div
                bind:this={element_item[
                  Object.keys(
                    $archive.record[selected_record].player_data.collection,
                  ).length + 1
                ]}
                style="background-color:var(--{item[
                  $archive.record[selected_record].player_data.souvenir
                ].type})"
              >
                <icon
                  class="icon-{$archive.record[selected_record].player_data
                    .souvenir}"
                ></icon>
              </div>
            {/if}
          </div>
        {/if}
      </div>
    {/if}
  </div>
  <div class="records">
    {#if $archive.record.length == 0}
      <txt style="font-size:30px;font-family:remark;color:#555;">尚无游玩记录</txt>
    {/if}
    {#each window.deepCopy($archive.record).reverse() as record, i (i)}
      <div class="tip" on:click={(_) => seleteRecord(i)}>
        <img src="/img/role/dairi/{record.player_data.role}.webp" />
        <div>
          <txt>{record.name}</txt>
          <txt class="time"
            >{record.player_data.chance.name} • {record.time}</txt
          >
        </div>
      </div>
    {/each}
  </div>
  <div class="achievements"></div>
  <div class="remind">右键回到标题界面</div>
</div>

<style>
  .body {
    --bg: #ccc;
    background-color: #f5f9f4;
    background-size:
      25px 25px,
      25px 25px;
    background-image: linear-gradient(90deg, var(--bg) 1px, transparent 2px),
      linear-gradient(0, var(--bg) 1px, transparent 2px);
  }
  .archive {
    flex-direction: row;
  }
  .container {
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE 10+ */
  }
  .container::-webkit-scrollbar {
    display: none; /* Chrome Safari */
  }

  .record_body {
    flex-direction: row;
  }
  .record_index {
    width: 140px;
    height: 250px;
    padding-left: 20px;
  }
  .record_index > txt {
    font-size: 24px;
    cursor: pointer;
  }
  .record_index > txt.selected:after,
  .record_index > txt:hover:after {
    content: " >";
  }

  .statistics > div {
    flex-direction: row;
    justify-content: space-between;
    font-size: 18px;
    line-height: 30px;
    width: 300px;
  }
  .item,
  .card {
    flex-wrap: wrap;
    flex-direction: row;
    align-content: flex-start;
    justify-content: center;
    width: 1045px;
    height: 450px;
    --blue: rgb(103, 186, 255);
    --purple: #c84df0;
    --green: #60d560;
    --red: #e55c5c;
    --gold: #ffe661;
    --shadow: #77777768;
  }
  .item icon {
    margin: auto;
    font-size: 36px;
    color: white;
    text-shadow:
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow),
      0 0 3px var(--shadow);
  }
  .item > div {
    width: 48px;
    height: 48px;
    margin: 4px;
    border-radius: 50%;
    box-sizing: border-box;
    position: relative;
    cursor: pointer;
    transition: 0.2s;
    filter: drop-shadow(0 0 3px rgba(136, 136, 136, 0.859));
  }
  .item txt {
    font-size: 16px;
    position: absolute;
    right: -12px;
    bottom: -4px;
    font-family: title;
    color: white;
    text-shadow:
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888;
    text-align: center;
    width: 32px;
  }
  .item > div:hover {
    transform: scale(1.1);
  }

  .remind {
    opacity: 0.7;
    position: absolute;
    top: 10px;
    font-size: 20px;
    font-family: normal;
    color: white;
    text-shadow:
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666;
  }

  .paper_close {
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .event_fold {
    position: absolute;
    font-size: 80px;
    top: 0;
    right: 20px;
    z-index: 1;
    font-family: "write";
  }
  .event_fold:hover {
    transform: scale(1.2);
  }
  .paper {
    font-family: "remark";
    color: #555;
    width: 800px;
    height: 640px;
    background: linear-gradient(to bottom, white 29px, #00b0d7 1px);
    margin: auto;
    background-size: 100% 30px;
    position: absolute;
    border-radius: 5px;
    box-shadow: 0 0 16px 10px rgba(0, 0, 0, 0.2);
    transform: translateY(20px);
    z-index: 1;
  }
  .paper::before {
    content: "";
    display: block;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 100px;
    height: 100%;
    width: 1px;
    background-color: #db4034;
  }
  .paper::after {
    content: "";
    height: 120px;
    width: 100%;
    background: white;
    position: absolute;
  }
  .paper > .title {
    z-index: 1;
    font-size: 40px;
    margin-top: 100px;
    margin-left: 120px;
    flex-direction: row;
    align-items: baseline;
  }
  .paper > .title > span {
    font-size: 20px;
  }

  .tip {
    --width: 320px;
    flex-direction: row;
    height: 76px;
    width: var(--width);
    align-items: center;
    color: #777;
    font-family: remark;
    font-size: 20px;
    box-sizing: border-box;
    background-image: radial-gradient(
        circle at 1px 8px,
        transparent 6px,
        white 6px,
        white 0px
      ),
      radial-gradient(
        circle at calc(var(--width) / 2) 8px,
        transparent 6px,
        white 6px,
        white 0px
      );
    background-size: calc(var(--width) / 2) 15px;
    background-position:
      0 0,
      calc(var(--width) / 2) 0;
    background-repeat: no-repeat repeat;
    transition: 0.3s;
    cursor: pointer;
    filter: drop-shadow(0 0 4px #777);
    padding-left: 10px;
    margin: 5px;
  }
  .tip:hover {
    transform: scale(1.1);
  }
  .tip img {
    height: 48px;
    border-radius: 50%;
    margin: 0 10px;
  }
  .tip .time {
    font-size: 16px;
    color: #888;
  }
</style>
