const cirno = {
  name: "冰之妖精",
  intro: "冷酷的智者",
  detail: "奇数回合攻击附加[迟缓]，偶数回合攻击附加[疲倦]。",
  type: "normal",
  scene: ["shrine", "town"],
  growth: {
    health: 1,
    attack: 1,
    speed: 1.2
  },
  action: (Admin, index) => {
    Admin.role.damage.target(0).from(index).by({
      type: "scale",
      value: 100,
    });
    if (Admin.role.event.round.count % 2 == 0) Admin.role.buff.of(0).add("tardy", 2);
    else Admin.role.buff.of(0).add("tired", 1);
  }
}

export default cirno;