<script>
  import { onMount, beforeUpdate, afterUpdate } from "svelte";
  import { fade, scale } from "svelte/transition";
  import { Explain, Admin } from "../store";

  let title;
  let detail;
  let color;
  let x;
  let y;
  let visibility = "hide";
  let E;

  $Explain = explain;
  const handle = {
    move: (e, info, c) => {
      title = info.name;
      detail = info.detail;
      color = c;
      x = document.body.clientWidth - e.x > 240 ? e.x + 5 : e.x - 230;
      y = e.y - E.offsetHeight - 5;
    },
    show: (e, info, c) => {
      visibility = "show";
      title = info.name;
      detail = info.detail;
      color = c;
      if ($Admin.mobile) {
        if (!e.x) e.x = e.touches[0].pageX;
        if (!e.y) e.y = e.touches[0].pageY;
        let root = document.getElementById("root");
        x =
          (e.y -
            (document.body.clientHeight / $Admin.mobile - root.clientWidth) /
              2) /
            $Admin.mobile -
          20;
        y = root.clientHeight - e.x / $Admin.mobile - 5;
        setTimeout((_) => (window.ontouchstart = handle.hide));
      } else {
        x = document.body.clientWidth - e.x > 240 ? e.x + 5 : e.x - 230;
        y = e.y - 5;
      }
    },
    hide: (_) => {
      window.ontouchstart = null;
      visibility = "hide";
      title = null;
    },
  };

  onMount(function () {
    if ($Admin.mobile) {
      E.ontouchstart = handle.hide;
    }
  });
  afterUpdate(function () {
    if (E) {
      E.onmouseenter = handle.hide;
      if ($Admin.mobile) y -= E.clientHeight;
    }
  });

  function explain(element) {
    return {
      color: function (color) {
        this.Color = color;
        return this;
      },
      with: function (info) {
        let color = this.Color ? this.Color : "blue";
        this.color = false;
        if (element && info) {
          element.ontouchstart = (e) => handle.show(e, info, color);
          if (!$Admin.mobile) {
            element.onmouseenter = (e) => handle.show(e, info, color);
            element.onmousemove = (e) => handle.move(e, info, color);
            element.onmouseleave = (_) => handle.hide();
          }
        }
      },
    };
  }
</script>

{#if visibility == "show" || $Admin.mobile}
  <div
    class="explain"
    style="left:{x}px;top:{y}px;visibility:{title ? 'visible' : 'hidden'}"
    bind:this={E}
    in:fade={{ duration: 100 }}
    out:fade={{ duration: 100 }}
  >
    <span style="background-color:var(--{color})">{title}</span>
    <a>{detail}</a>
  </div>
{/if}

<style>
  .explain {
    z-index: 9;
    position: fixed;
    color: white;
    text-shadow:
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666;
    box-shadow: 0 0 5px 4px rgba(255, 255, 255, 0.5);
  }

  .explain > span {
    --blue: rgb(103, 186, 255);
    --purple: #c84df0;
    --green: #60d560;
    --red: #e55c5c;
    --gold: #ffe661;
    width: 225px;
    height: 26px;
    font-size: 18px;
    line-height: 24px;
    font-family: normal;
    padding-left: 10px;
  }

  .explain > a {
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(8px);
    width: 225px;
    padding: 10px;
    font-size: 14px;
    font-family: remark;
    white-space: pre-wrap;
  }
</style>
