<script>
  import { Admin, Explain } from "../../store";
  import { onMount, beforeUpdate } from "svelte";
  import Collection from "../../data/collection.json";
  import Equipment from "../../data/equipment.js";
  import Souvenir from "../../data/souvenir.json";
  import Spellcard from "../../data/spellcard.js";
  import Consumable from "../../data/consumable.json";
  import Basecard from "../../data/basecard.js";
  import Card from "../card.svelte";

  export let info;

  let product;
  const element = [];

  if (info.cache.product) product = info.cache.product;
  else info.cache.product = summonProduct();

  onMount(function () {
    element.forEach((e, i) => {
      let info = {
        name: "价格",
        detail: product[i].piece
      };
      $Explain(e).with(info);
      e.onclick = (_) => purchase(product[i]);
    });
  });

  function summonProduct() {
    let source = Object.keys(Basecard).rd().splice(0, $Admin.data.sugar + 3);
    let res = source.map((c) => {
      return {
        key: c,
        piece: 450,
        sold: false
      };
    });
    product = $Admin.discount(res);
    return res;
  }
  function purchase(p) {
    if (!p.sold) {
      if ($Admin.data.coin >= p.piece) {
        product = product;
        $Admin.data.coin -= p.piece;
        p.key.increaseOf($Admin.data.card);
        msg({ content: `已购买[${Basecard[p.key].name}]` });
        p.sold = true;
      } else msg({ content: "硬币不足" });
    }
  }
</script>

<div class="content">
  <txt>{info.detail}</txt>
  <div class="products">
    {#each product as p, i (i)}
    <div bind:this={element[i]} class="product">
      <Card key={p.key} cover={p.sold && {
        name: "空",
        detail: "已售罄"
      }} />
    </div>
    {/each}
  </div>
</div>

<style>
  .content {
    z-index: 1;
    width: 600px;
    height: 500px;
    margin: auto;
  }
  txt {
    font-size: 20px;
    line-height: 30px;
    position: relative;
    top: 2px;
    margin-left: 5px;
  }
  .products {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 600px;
    margin-top: 10px;
  }
  .product {
    cursor:pointer;
    margin: 0px 10px;
  }
</style>
