const lilywhite = {
  name: "报春的妖精",
  intro: "拥有告知春天到来程度的能力",
  detail: "不受任何效果影响，且攻击造成随机伤害。",
  type: "normal",
  scene: ["shrine", "forest"],
  growth: {
    health: 0.8,
    attack: 1.2,
    speed: 1
  },
  action: (Admin, index) => {
    let r = Math.random();
    Admin.role.damage.target(0).from(index).by({
      type: "scale",
      value: 200 - r * 100,
    });
    Admin.enermy.buff.of(index).clearAll();
  }
}

export default lilywhite;